                                        "titulo" : "Sobre Nosotros",
                                        "descripcion" : "Ante la necesidad de satisfacer la demanda surgida, y contando con personas con más de una década de experiencia en el rubro emerge la idea de la creación de NG GROUP S.A. La República del Paraguay es una gran productora de cereales, granos, y sus derivados, y para ello le brindamos soluciones plenas para la logística de comercialización de dichos productos. Muy sabido es que nuestro país ofrece posibilidades de negocios estables y seguros debido a su estabilidad macroeconómica, por lo que le invitamos a integrarse al giro comercial nacional para generar beneficios económicos mutuos. El correcto manejo de los productos, permitiendo mantener la calidad de los mismos da como resultado un manejo positivo de los costos, favoreciendo la ampliación del marco comercial. Desde ya, muy dispuestos a servirle, le instamos a no dudar en ponerse en contacto, de modo a evacuar cualquier consulta de la manera más eficiente y profesional posible."
