                                      "introduccion":"Promover la participación de los comités de productores, acopiadores, de productores del Departamento Central y con énfasis de la ciudad de Luque; a fin de lograr una cooperación integral, actualizaciones permanentes relacionadas a las producciones y desarrollo, de los recursos humanos capaces de seguir creciendo de manera sustentable y con conocimientos responsables en el campo psicosocial, político, económico, coadyuvando así en el bienestar general de la ciudadanía luqueña y no luqueñas, con el afán de extender sus metas a las demás ciudades/distritos del Departamento Central, con grandes enfoques de expansión en el territorio nacional, e incluso a nivel internacional.",
