import React from "react";
// @material-ui/core components
import contenido from '../../../../config/config';
import { checkIdioma } from '../../../../services/services';
import Carrusel from "../Componentes/Carrusel.js";

export default function Muestras(props) {
	let idioma = checkIdioma();
	const ContenidoMuestras = contenido.muestras[idioma];
	const Configuracion = contenido.muestras.configuracion;

    return ( 
    	<React.Fragment>
	        <Carrusel contenido={ContenidoMuestras} configuracion={Configuracion}/>
    	</React.Fragment>
    )
}
